import React, { useState, useEffect } from 'react'
import styles from './Collection.module.css'
import Navbar from '../Navbar/Navbar'
import { useParams, Link } from "react-router-dom"
import { Dropdown } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { useHistory } from "react-router-dom";


export default function Collection() {

    const { id } = useParams()
    const history = useHistory()

    const [collectionArray, setCollectionArray] = useState([])
    const [singleAsset, setSingleAsset] = useState([])
    const [collectionStats, setCollectionStats] = useState('')
    const [pageNumber, setPageNumber] = useState(10)
    let [orderBy, setOrderBy] = useState('asc')
    let [sales, setSales] = useState('sale_price')
    let [filteredApi, setFilteredApi] = useState(false)
    let [input, setInput] = useState('')


    const tokenAddress = id
    // const direction = 'asc'
    // const orderBy = 'sale_price'

    let api = `https://api.opensea.io/api/v1/assets?asset_contract_addresses=${tokenAddress}&offset=0&limit=${pageNumber}`
    const api2 = `https://api.opensea.io/api/v1/asset_contract/${tokenAddress}`
    let filterApi = `https://api.opensea.io/api/v1/assets?asset_contract_addresses=${tokenAddress}&order_by=${sales}&order_direction=${orderBy}&offset=0&limit=${pageNumber}`

    const options = { method: 'GET' }
    let mainApi

    if (filteredApi === false) {
        mainApi = api
    } else {
        mainApi = filterApi
    }

    async function fetchData() {
        const data = await fetch(mainApi, options)
        const response = await data.json()
        if (data.status !== 400) {
            setCollectionArray(response.assets)
        }
        else {
            history.push('/404')
        }
    }
    async function fetchSingle() {
        let data = await fetch(api2, options)
        const response = await data.json()
        if (data.status !== 500) {
            setSingleAsset(response.collection)
        }
        else {
            history.push('/404')
        }
    }

    useEffect(() => {
        fetchData()
        fetchSingle()
    }, [pageNumber, orderBy, sales, filteredApi])


    let { slug } = singleAsset

    const api3 = `https://api.opensea.io/api/v1/collection/${slug}/stats`

    useEffect(() => {
        fetch(api3, options)
            .then(res => res.json())
            .then(data => setCollectionStats(data.stats))
    }, [api3])

    function increaseNum() {
        setPageNumber(pageNumber + 10)
    }
    let grey_image = '/nft-spotter/imgs/grey.png'
    let { banner_image_url, image_url, name, safelist_request_status, description } = singleAsset

    let { floor_price, num_owners, count, seven_day_volume, one_day_sales, average_price, seven_day_sales, one_day_volume } = collectionStats
    const sevDayVol = seven_day_volume !== null ? String(seven_day_volume).match(/^-?\d+(?:\.\d{0,2})?/) : 'undefined'
    const oneDayVol = one_day_volume !== null ? String(one_day_volume).match(/^-?\d+(?:\.\d{0,2})?/) : 'undefined'
    const avgPrice = average_price !== null ? String(average_price).match(/^-?\d+(?:\.\d{0,2})?/) : 'undefined'
    const floorPrice = floor_price !== null ? String(floor_price).match(/^-?\d+(?:\.\d{0,4})?/) : 'undefined'


    let display =
        (
            <div className={styles.mainWrapper}>
                <div className={styles.bannerDiv}>
                    <img src={banner_image_url !== null ? banner_image_url : grey_image} className={styles.bannerImage} alt="banner" />
                </div>
                <div className={styles.collectionDiv}>
                    <div style={{ position: 'relative' }}>
                        <img src={image_url} className={styles.collectionImage} alt="" />
                        <img src="/nft-spotter/imgs/verified-user.png" alt="" style={{ display: safelist_request_status === "verified" ? 'block' : 'none', width: '35px', position: 'absolute', bottom: 0, right: 0 }} />
                    </div>

                    <h2 style={{ fontWeight: 'bold', margin: '10px', fontSize: '2.5rem' }}>{name}</h2>

                    <p style={{ width: '60%', padding: '10px', textAlign: 'center' }}>{description}</p>
                </div>

            </div>
        )

    const biggerText = { fontSize: '1.5rem', fontWeight: 'bold' }
    const ethSymbol = { width: '25px', height: '25px' }

    let priceDisplay =
        (
            <div className={styles.stats}>
                <div className={styles.statsDiv}>
                    <div className={styles.statsBox}>
                        <div>Items:</div>
                        <div style={biggerText}>{count}</div>
                    </div>
                    <div className={styles.statsBox}>
                        <div>Owners:</div>
                        <div style={biggerText}>{num_owners}</div>
                    </div>
                </div>
                <div className={styles.statsDiv}>
                    <div className={styles.statsBox}>
                        <div>Floor Price:</div>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                            <div style={biggerText}>{floorPrice}</div>
                        </div>
                    </div>
                    <div className={styles.statsBox}>
                        <div>Average Price</div>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                            <div style={biggerText}>{avgPrice}</div>
                        </div>
                    </div>
                </div>
                <div className={styles.statsDiv}>
                    <div className={styles.statsBox}>
                        <div>1d Volume</div>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                            <div style={biggerText}>{oneDayVol}</div>
                        </div>
                    </div>
                    <div className={styles.statsBox}>
                        <div>7d Volume:</div>
                        <div style={{ display: 'flex', alignItems: 'center' }}>
                            <img src="/nft-spotter/imgs/eth.svg" style={ethSymbol} alt="" />
                            <div style={biggerText}>{sevDayVol}</div>
                        </div>
                    </div>
                </div>
                <div className={styles.statsDiv}>
                    <div className={styles.statsBox}>
                        <div>Sales Today:</div>
                        <div style={biggerText}>{one_day_sales}</div>
                    </div>
                    <div className={styles.statsBox}>
                        <div>7d Sales:</div>
                        <div style={biggerText}>{seven_day_sales}</div>
                    </div>
                </div>
            </div>
        )


    const handleOrder = (order) => {
        setOrderBy(order)
        setFilteredApi(true)
    }

    const handleFilter = (filter) => {
        setSales(filter)
        setFilteredApi(true)
    }

    const removeFilters = () => {
        setFilteredApi(false)
    }

    const handleChange = (e) => {
        setInput(e.target.value)
    }
    const handleSubmit = () => {
        history.push(`/assets/${tokenAddress}/${input}`)
    }

    const active = { fontWeight: 'bold' }



    let filter = (
        <div className="d-flex justify-content-center">
            <h2>Filter</h2>
            <Dropdown style={{ margin: '0 2px' }}>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                    Order by
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item style={orderBy === 'asc' && filteredApi === true ? active : null} onClick={() => handleOrder('asc')}>Ascending</Dropdown.Item>
                    <Dropdown.Item style={orderBy === 'desc' && filteredApi === true ? active : null} onClick={() => handleOrder('desc')}>Descending</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
            <Dropdown style={{ margin: '0 2px' }}>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                    Sales
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item style={sales === 'sale_price' && filteredApi === true ? active : null} onClick={() => handleFilter('sale_price')}>Highest Price Transactions</Dropdown.Item>
                    <Dropdown.Item style={sales === 'sale_count' && filteredApi === true ? active : null} onClick={() => handleFilter('sale_count')}>Recently Sold</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>

            <button onClick={removeFilters} className="btn btn-danger mx-1" style={{ height: '38px' }}>Remove Filters</button>

            <form className={`d-flex ${styles.form}`}>
                <div class="input-group mb-3">
                    <input onChange={handleChange} type="text" class="form-control" placeholder="Search Token" />
                    <button onClick={handleSubmit} class="btn btn-secondary" type="button" id="button-addon2"><FontAwesomeIcon icon={faSearch} /></button>
                </div>
            </form >
        </div >
    )


    let nftDisplay

    nftDisplay = collectionArray.map(item => {
        let { id, image_url, token_id } = item
        return (
            <Link to={`/assets/${tokenAddress}/${token_id}`}>
                <div key={id} className={`card col-2 col-sm-5 col-md-4 ${styles.colCard}`}>
                    <img src={image_url === '' ? grey_image : image_url} width="250" className={`card-img-top ${styles.image}`} alt="" />
                    <div className="card-body" style={{ height: 'contain', width: '100%' }}>
                        <div className={styles.name}>{name}</div>
                        <div className="tokenId"># {token_id}</div>
                    </div>
                </div>
            </Link>
        )
    })

    return (
        <>
            <Navbar />

            {display}

            {priceDisplay}

            {filter}

            <div className="container-fluid">
                <div className={styles.cardDisplay}>
                    {nftDisplay}
                </div>
            </div>



            <div class="d-grid gap-2 d-md-block">
                <button onClick={increaseNum}
                    style={{ display: pageNumber < 50 ? 'block' : 'none', margin: '30px auto', width: '400px' }}
                    className="btn btn-info"
                    type="button">
                    Load More
                </button>
            </div>

        </>
    )
}
