import React from 'react'
import { Link } from 'react-router-dom'
import styles from './Card.module.css'
import { useHistory } from 'react-router'


export default function Cards({ topCollections }) {

    const history = useHistory();

    let display


    display = topCollections.map((col, index) => {
        let { id, name, pic, sum } = col

        if (!pic) {
            pic = 'https://static.vecteezy.com/system/resources/previews/002/473/294/non_2x/nft-symbol-non-fungible-token-icon-free-vector.jpg'
        }
        index += 1

        let newSum = Math.floor(sum)
        sum = newSum.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        let slug = name.trim().replace(/\s+/g, '-').toLowerCase()


        return (
            <div key={id} className={`col-xl-3 col-md-6 col-12 mb-4 position-relative ${styles.colCard}`}>
                <div className={styles.index}>{index} </div>
                <img className={styles.image} width="150" src={pic} alt="" />
                <div>
                    <Link to={`/collections/${id}`} style={{ color: 'white', textDecoration: 'none' }}>
                        <div>{name}</div>
                        <div className={styles.sum}>${sum}</div>
                    </Link>
                </div>
            </div>

        )
    })

    return (
        <>
            <div className={styles.topDiv}>
                <h3>Top Collections Today ðŸ‘‘</h3>
                <div className={`btn btn-secondary ${styles.btn} `} onClick={() => history.push('/collections')}>See All</div>
            </div>
            {display}
        </>
    )
}
