import 'bootstrap/dist/css/bootstrap.min.css'
import "bootstrap/dist/js/bootstrap"
import React, { useEffect, useState } from "react"
import './App.css'
import { HashRouter as Router, Switch, Route } from "react-router-dom"
import Cards from './components/Cards/Cards'
import HotCollections from "./components/HotCollections/HotCollections"
import AllCollections from "./components/AllCollections/AllCollections"
import Navbar from "./components/Navbar/Navbar"
import Watchlist from "./components/Watchlist/Watchlist"
import Collection from "./components/Collection/Collection"
import NFTCard from "./components/NFTCard/NFTCard"
import ErrorPage from "./components/ErrorPage/ErrorPage"
import Footer from './components/Footer/Footer'




function App() {

  const [topCollections, setTopCollections] = useState([])
  const [hotCollections, setHotCollections] = useState([])
  const [allCollections, setAllCollections] = useState([])

  const api = "https://rarible-data-scraper.p.rapidapi.com/top_collection/1/16"
  const api2 = "https://rarible-data-scraper.p.rapidapi.com/hot_collection"
  const api3 = "https://rarible-data-scraper.p.rapidapi.com/top_collection/1/100"
  const headers = {
    "method": "GET",
    "headers": {
      "x-rapidapi-host": "rarible-data-scraper.p.rapidapi.com",
      "x-rapidapi-key": "27b75a3147msh07900745f4edceep15fcb0jsn727c84d5ae66"
    }
  }


  useEffect(() => {
    (async function () {
      let data = await fetch(api, headers)
        .then(response => response.json())
      setTopCollections(data.list)
    })()
  }, [setTopCollections])


  useEffect(() => {
    (async function () {
      let data = await fetch(api2, headers)
        .then(response => response.json())
      setHotCollections(data.list)
    })()
  }, [setTopCollections])


  useEffect(() => {
    (async function () {
      let data = await fetch(api3, headers)
        .then(response => response.json())
      setAllCollections(data.list)
    })()
  }, [setTopCollections])



  return (


    // Header
    <>
      <Router >
        <Switch>
          <Route path="/" exact>
            <Navbar />

            {/* Top Collections  */}
            <div className="container">
              <div className="row">
                <Cards topCollections={topCollections} />
              </div>
            </div>

            {/* Hot Collections  */}
            <div className="container">
              <HotCollections hotCollections={hotCollections} />
            </div>

            <div className="container">
              <div className="row">
                <Watchlist />
              </div>
            </div>

            <Footer />

          </Route>
          <Route exact path="/collections" >
            <AllCollections allCollections={allCollections} />
          </Route>


          <Route path="/collections/:id">
            <Collection />
          </Route>

          <Route path="/assets/:id/:token">
            <NFTCard />
          </Route>

          <Route path="/404">
            <ErrorPage />
          </Route>
        </Switch>
      </Router>


    </>

  );
}

export default App
